import React from 'react';
import { InputNumber } from 'antd';

// Default inputs make no use of meta (which comes from redux-form) and
// react will warn about invalid props so exclude it here
/**
 * See [InputNumber](https://ant.design/components/input-number/) for props available
 */
export default function NumberWidget({ meta, ...rest }) {
    return <InputNumber {...rest} />;
}
